.grid figure figcaption,
.grid figure figcaption > a {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}


.grid figure h2,
.grid figure p {
  margin: 0;
}

/*---------------*/
/***** Zoe *****/
/*---------------*/

figcaption {
  padding-top: .75em;
}

figure.effect-zoe {
  figcaption {
    top: auto;
    bottom: 0;
    padding: 1rem;
    height: 3.75rem;
    background: #fff;
    color: #3c4a50;

    @media (min-width:$sm-breakpoint) {
      -webkit-transition: -webkit-transform 0.35s;
      transition: transform 0.35s;
      -webkit-transform: translate3d(0,100%,0);
      transform: translate3d(0,100%,0);
    }
  }

  h2 {
    float: left;
  }
}

figure.effect-zoe p.icon-links a {
  float: right;
  color: #3c4a50;
  font-size: 1.4em;
}

figure.effect-zoe:hover p.icon-links a:hover,
figure.effect-zoe:hover p.icon-links a:focus {
  color: #252d31;
}

figure.effect-zoe p.description {
  position: absolute;
  bottom: 1rem;
  padding: 2rem .3rem 3.9rem;
  color: #fff;
  text-transform: none;
  font-size: 19px;
  font-weight: 600;
  text-shadow: 0 0 5px #000;
  font-family: Roboto, sans-serif;
  -webkit-transition: 0.35s;
  transition: 0.35s;
  -webkit-backface-visibility: hidden; /* Fix for Chrome 37.0.3062.120 (Mac) */

  background: -moz-linear-gradient(top,  rgba(0,0,0,0) 0%, rgba(0,0,0,0.30) 50%, rgba(0,0,0,0) 100%); /* FF3.6+ */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(0,0,0,0)), color-stop(50%,rgba(0,0,0,0.30)),  color-stop(100%,rgba(0,0,0,0))); /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(0,0,0,.30) 50%,rgba(0,0,0,0) 100%); /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(top, rgba(0,0,0,0) 0%,rgba(0,0,0,0.30) 50%, rgba(0,0,0,0) 100%); /* Opera 11.10+ */
  background: -ms-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(0,0,0,0.30) 50%, rgba(0,0,0,0) 100%); /* IE10+ */
  background: linear-gradient(to bottom,  rgba(0,0,0,0) 0%,rgba(0,0,0,0.30) 50%, rgba(0,0,0,0) 100%); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#bf0000',GradientType=0 ); /* IE6-9 */


  @media (min-width:$xs-breakpoint) {
    top: 3.8em;
  }
  @media (min-width:$sm-breakpoint) {
    top: 1.5em;
  }
}

figure.effect-zoe:hover p.description {
  @media (min-width:$sm-breakpoint) {
    top: .5em;
  }
}

figure.effect-zoe h2,
figure.effect-zoe p.icon-links a {
  @media (min-width: $sm-breakpoint) {
    -webkit-transition: -webkit-transform 0.35s;
    transition: transform 0.35s;
    -webkit-transform: translate3d(0,200%,0);
    transform: translate3d(0,200%,0);
  }
}

figure.effect-zoe p.icon-links a span::before {
  display: inline-block;
  padding: 8px 10px;
  font-family: 'feathericons';
  speak: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

figure.effect-zoe h2 {
  display: inline-block;
}

figure.effect-zoe:hover p.description {
  opacity: 1;
}

figure.effect-zoe:hover figcaption,
figure.effect-zoe:hover h2,
figure.effect-zoe:hover p.icon-links a {
  -webkit-transform: translate3d(0,0,0);
  transform: translate3d(0,0,0);
}

figure.effect-zoe:hover h2 {
  -webkit-transition-delay: 0.05s;
  transition-delay: 0.05s;
}

figure.effect-zoe:hover p.icon-links a:nth-child(3) {
  -webkit-transition-delay: 0.1s;
  transition-delay: 0.1s;
}

figure.effect-zoe:hover p.icon-links a:nth-child(2) {
  -webkit-transition-delay: 0.15s;
  transition-delay: 0.15s;
}

figure.effect-zoe:hover p.icon-links a:first-child {
  -webkit-transition-delay: 0.2s;
  transition-delay: 0.2s;
}

@media screen and (max-width: 50em) {
  .content {
    padding: 0 10px;
    text-align: center;
  }
  .grid figure {
    display: inline-block;
    float: none;
    margin: 0 auto;
    width: 100%;
  }
}


.card-container {
  padding: .6rem 0 .1rem;
  margin: .4rem 0;
  background: white;

  @media (min-width:$xs-breakpoint) {
    padding: .6rem;
  }
  .fa-facebook, .fa-twitter, .fa-comment, .fa-envelope {
    margin: .5rem 1rem 0;
    font-size: 1.1rem;
    cursor: pointer;
  }
}

.card {
  max-width: 500px;
  margin: 0 auto;
  cursor: pointer;
  margin-bottom: .3rem;
  box-shadow: 0px .6px 0px rgba(100,100,100,.2);
  -moz-box-shadow: 0px .6px 0px rgba(100,100,100,.2);
  -webkit-box-shadow: 0px .6px 0px rgba(100,100,100,.2);
}

.card-block {
  padding: .8rem 1rem;
}
.card-title {
  font-size: 1rem;
  margin-bottom: 0;
  font-weight: 600;
}

.date-footer {
  font-size: .9rem;
  padding-bottom: .3rem;
  margin-bottom: .2rem;
  border-bottom: .0625rem solid $date-bottom-border;
}

.icon-links {
  margin-bottom: 0;
}

.mix {
  @media (max-width: $xs-breakpoint - 1) {
    background: white;
    margin: .3rem 0;
    padding-left: .5rem;
    padding-right: .5rem;
  }
}